html {
  --color-active: #00acdc;
  font-family: Roboto, Arial, sans-serif;
}

body {
  margin: 0;
}

header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.container {
  flex-direction: row;
  display: flex;
}

.header_logo {
  margin: 10px 0 auto 50px;
}

.nav {
  margin-right: 50px;
  display: flex;
  position: relative;
}

.nav__item {
  margin-right: 20px;
}

.nav__last__item {
  margin: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_button {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

a.nav_button {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav_button:hover, .apple_button {
  color: var(--color-active);
}

.nav_button.is-active:after {
  content: "";
  background-color: var(--color-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
